	module.exports = {"data":{"wordpressPost":{"title":"Antipsychotics, vitamin D and alcohol consumption: Most read articles in December","content":"<p>December&#8217;s most read articles include a cohort study by Marston  <em>et al.</em> on the prescribing of antipsychotics in UK primary care. We also have a paper which focuses on the impact of Australia’s introduction of tobacco plain packs by Dunlop <em>et al., </em>as well as featuring articles by Shi <em>et al </em>and their paper on how equity is addressed in clinical practice guidelines, and<em> Caulfield et al’s </em>paper on representations of the health value of vitamin D supplementation in newspapers.</p>\n<p>&nbsp;</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Marston <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006135.full\">Prescribing of antipsychotics in UK primary care: a cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Dunlop <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e005836.full\" target=\"_blank\">Impact of Australia&#8217;s introduction of tobacco plain packs on adult smokers’ pack-related perceptions and responses: results from a continuous tracking survey</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Anderson <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006544.full\">Prescriber barriers and enablers to minimising potentially inappropriate medications in adults: a systematic review and thematic synthesis</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Yri <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/4/e004376.full\">Cognitive function in idiopathic intracranial hypertension: a prospective case–control study</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Amer<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006671.full\">Effects of dietary sodium and the DASH diet on the occurrence of headaches: results from randomised multicentre DASH-Sodium clinical trial</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Bull <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/11/e006046.full\">Are interventions for low-income groups effective in changing healthy eating, physical activity and smoking behaviours? A systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Shi <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e005660.full\">How equity is addressed in clinical practice guidelines: a content analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Caulfield <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006395.full\">Representations of the health value of vitamin D supplementation in newspapers: media content analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">King <em>et al. </em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e005473.full\" target=\"_blank\">Redesigning the ‘choice architecture’ of hospital prescription charts: a mixed methods study incorporating in situ simulation testing</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Jensen<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/9/e005462.full\">Habitual alcohol consumption associated with reduced semen quality and changes in reproductive hormones; a cross-sectional study among 1221 young Danish men</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"342521c9-18e5-51cf-9835-596078b347d7"}}

